"use client";

import { useEffect, useState } from "react";
import Image from "next/image";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Autoplay } from "swiper/modules";
import "swiper/css";
import "swiper/css/pagination";

export default function TestimonialCarousel() {
  const [testimonials, setTestimonials] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("/data/OurProducts.json");
      const data = await res.json();
      setTestimonials(data);
    };

    fetchData();
  }, []);

  return (
    <div className="py-10 px-4 bg-[#EDE6DB] rounded-3xl relative">
      {/* Centered Title */}
      <h2 className="text-[#125C3A] text-2xl sm:text-3xl font-bold text-center mb-8">
       OUR PRODUCTS
      </h2>

      <Swiper
        modules={[Pagination, Autoplay]}
        spaceBetween={20}
        slidesPerView={1}
        pagination={{ clickable: true }}
        autoplay={{ delay: 3000 }}
        breakpoints={{
          250: { slidesPerView: 1 },
          500: { slidesPerView: 2 },
          750: { slidesPerView: 3 },
          1024: { slidesPerView: 4 },
        }}
        className="pb-10"
      >
        {testimonials.map((item, index) => (
          <SwiperSlide key={item.id}>
            <div
              className={`rounded-xl p-4 shadow-md text-center h-full transition duration-300 my-5 ${
                index % 2 === 0
                  ? "bg-gradient-to-b from-green-100 to-green-700"
                  : "bg-gradient-to-b from-yellow-100 to-yellow-600"
              }`}
            >
              <div className="w-full h-40 relative mb-4">
                <Image
                  src={item.img}
                  alt={item.title}
                  layout="fill"
                  objectFit="contain"
                  className="rounded"
                />
              </div>
              <h3 className="text-lg font-semibold text-gray-800">{item.title}</h3>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>

      {/* Custom Pagination Style Override */}
      <style jsx global>{`
        .swiper-pagination {
          bottom: -5px !important;
          text-align: center;
        }

        .swiper-pagination-bullet {
          background-color: #555 !important;
          opacity: 1;
        }

        .swiper-pagination-bullet-active {
          background-color: #000 !important;
        }
      `}</style>
    </div>
  );
}
